.gallery {
  @extend %padding;

  &__title {
    @extend %h2;
  }

  &__place {
    display: grid;
    column-gap: 20px;
    grid-template-columns: 1fr 1fr;
    min-height: 131px;
    margin-bottom: 16px;

    @include dekstop {
      height: 256px;
      column-gap: 24px;
      margin-bottom: 24px;

      &--first {
        grid-template-columns: 2fr 1fr;
      }

      &--second {
        grid-template-columns: 1fr 1fr;
      }

      &--third {
        grid-template-columns: 1fr 2fr;
      }

    }

  }

  &__image {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 8px;
    transition: transform $anim, box-shadow $anim;
    cursor: pointer;

    &:hover,
    &:focus {
      transform: scale(1.05);
      box-shadow: 0 13px 27px 0 $shadow-color;
    }

    &--marina1 {
      background-image: url(../images/photos/1.jpg);
    }

    &--marina2 {
      background-image: url(../images/photos/2.jpg);
    }

    &--edition1 {
      background-image: url(../images/photos/3.jpg);
    }

    &--edition2 {
      background-image: url(../images/photos/4.jpg);
    }

    &--faena1 {
      background-image: url(../images/photos/5.jpg);
    }

    &--faena2 {
      background-image: url(../images/photos/6.jpg);
    }
  }

  &_value {
    margin-bottom: 32px;

    @include dekstop {
      &:not(:last-child) {
        margin-bottom: 40px;
      }
    }

  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 56px;
    border: 2px solid $blue-color;
    border-radius: 8px;
    text-decoration: none;
    color: $blue-color;
    font-weight: bold;
    @extend %btn-action;

    @include dekstop {
      width: 150px;
      margin: 0 auto;
    }
  }
}
